import {modalContainer} from "../components.js";
import * as selectedBook from "../states/selectedBook.js";
import {updateBookContainer} from "../utils.js";
const readBtn = document.getElementById("read");
const deleteBtn = document.getElementById("delete");
readBtn?.addEventListener("click", (evt) => {
  const currentBook = selectedBook.getState();
  const booksFromStorage = JSON.parse(localStorage.getItem("books"));
  const index = booksFromStorage.findIndex((val) => val.id == currentBook.id);
  booksFromStorage[index] = {
    id: currentBook.id,
    author: currentBook.author,
    read: true,
    title: currentBook.title,
    pages: currentBook.pages
  };
  localStorage.setItem("books", JSON.stringify(booksFromStorage));
  updateBookContainer();
  modalContainer?.classList.replace("flex", "hidden");
});
deleteBtn?.addEventListener("click", (evt) => {
  const currentBook = selectedBook.getState();
  const filtered = JSON.parse(localStorage.getItem("books")).filter((val) => val.id !== currentBook.id);
  localStorage.setItem("books", JSON.stringify(filtered));
  updateBookContainer();
  modalContainer?.classList.replace("flex", "hidden");
});
