import {addBookForm, modalContainer} from "../components.js";
import {generateId, updateBookContainer} from "../utils.js";
addBookForm?.addEventListener("submit", (evt) => {
  evt.preventDefault();
  const title = document.getElementById("title");
  const author = document.getElementById("author");
  const pages = document.getElementById("pages");
  let currentBooks = JSON.parse(localStorage.getItem("books"));
  currentBooks.push({
    title: title.value,
    author: author.value,
    pages: pages.value,
    read: false,
    id: generateId()
  });
  localStorage.setItem("books", JSON.stringify(currentBooks));
  title.value = "";
  author.value = "";
  pages.value = "";
  modalContainer?.classList.add("hidden");
  updateBookContainer();
});
