import {overlay, modalContainer} from "../components.js";
import {showModal} from "../utils.js";
import * as selectedBook from "../states/selectedBook.js";
overlay?.addEventListener("click", (evt) => {
  modalContainer?.classList.add("hidden");
});
document.addEventListener("click", (evt) => {
  if (evt.target.matches("[data-toggle]")) {
    const id = evt.target.getAttribute("data-toggle");
    showModal(id);
    if (id === "dialog") {
      const target = evt.target;
      const targetBook = JSON.parse(localStorage.getItem("books")).find((val) => val.id == target.id);
      selectedBook.setState(targetBook);
    }
  }
});
